<template>
    
    <v-dialog
    transition="dialog-bottom-transition"
    max-width="400"
    >
        <template v-slot:activator="{ on, attrs }">
            <v-btn
                v-bind="attrs"
                v-on="on"
                color="accent"
                text
            >
                <i>
                    edit
                </i>
            </v-btn>
        </template>
        <template v-slot:default="dialog">
            <v-card>
            <v-toolbar
                dark
            >編輯購買記錄</v-toolbar>
            <v-card-text>
                <div class="pa-6 pb-0">

                    <v-form
                        ref="form"
                    >
                        <v-container fluid>
                            <v-row>
                                <v-col
                                cols="6"
                                sm="12"
                                class="pb-0"
                                >
                                    <v-select
                                        v-model="product"
                                        
                                        label="選擇商品"
                                        color="primary"
                                        required clearable

                                        v-if="$store.state.products.length > 0"
                                        :items="$store.state.products.map(e => { 
                                            return {
                                                text: e.named,
                                                value: e.id
                                            }
                                        })"
                                    ></v-select>
                                    <loading-small v-else></loading-small>
                                </v-col>
                                <v-col
                                cols="6"
                                sm="12"
                                class="py-0"
                                >
                                    <v-text-field
                                        v-model="num"
                                        :rules="rule"
                                        color="primary"
                                        label="購買數量"
                                        required clearable
                                    ></v-text-field>
                                </v-col>

                                <v-col cols="4" class="pt-6">
                                    總計Pv：
                                </v-col>
                                <v-col
                                cols="8"
                                class="py-0">
                                    <v-text-field
                                        v-model="resPv"
                                        :rules="rule"
                                        color="primary"
                                        required clearable disabled
                                    ></v-text-field>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-form>

                </div>
                <v-alert
                dense
                text

                class="ma-0"
                :type="alert_type"

                v-if="alert"
                >
                    {{ alert_msg }}
                </v-alert>
            </v-card-text>
            <v-card-actions class="justify-end">
                <v-btn
                    text
                    color="accent"
                    @click="doSubmit"
                >Submit</v-btn>
                <!-- v-btn
                    text
                    @click="dialog.value = false"

                    class="text-primary-dark"
                >Close</v-btn -->
            </v-card-actions>
            </v-card>
        </template>
    </v-dialog>

</template>

<script>
import user_data from '@/apis/mock/user_unit_data'
import LoadingSmall from '../Data/LoadingSmall.vue'

    export default {
  components: { LoadingSmall },
        name: '',
        props: {
            
            _item: {
                type: Object
            },
        },
        data() {

            return {
                product: null,
                num: null,
                
                rule: [ v => v > 0 || 'Value > 0, pls.' ],

                alert: false,
                alert_type: 'success',
                alert_msg: ''
            }
        },
        mounted() {
            this.senDef()
        },
        computed: {
            resPv() {
                let res = 0
                if (this.$store.state.products.length > 0) {
                    res = this.$store.state.products.filter(e => {
                        if (e.id == this.product) { return true } return false
                    })

                    res = res[0].pv * this.num
                }

                return res
            }
        },
        methods: {
            senDef() {
                this.num = this._item.num
                this.product = this._item.product.id
            },

            alertIt(msg, at = 'success') {

                if (at != 'success') {
                    this.alert_type = 'error'
                }
                this.alert = true
                this.alert_msg = msg


                setTimeout(() => {
                    this.alert = false
                }, 1500)
            },
            doSubmit() {
                let p = this.num

                if ((p == null))  {
                    this.alertIt('Input something, pls.', '')
                    return 
                }
                if (isNaN(p)) {
                    this.alertIt('Sir, it not an number.', '')
                    return 
                }

                this.submit(this.product, p)
            },
            async submit(product, num) {
                const id = this._item.id

                const data = { num, product }
                
                const res = await this.conn.put(
                    this.api.buys + '/' + id,
                    this.$store.state.token,
                    data
                )

                if (res) {
                    this.alertIt('更改成功！！！')
                    
                    this.$emit('single_Father', product, num)
                } 
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


