<template>
    
    <v-card>
        
        <table class="w-full text-center" v-if="buys.length > 0">
            <be-table-header></be-table-header>

            <be-table-item 
                v-for="(v, i) in buys" :key="i"

                :index="i"
                :item="v"

                :datas="buys"
            >
            </be-table-item>
        </table>

        <loading-huge v-else></loading-huge>
    </v-card>
</template>

<script>
import LoadingHuge from '../../../components/Data/LoadingHuge.vue'
import EditBuy from '../../../components/Modal/EditBuy.vue'
import BeTableHeader from './Inner/BeTableHeader.vue'
import BeTableItem from './Inner/BeTableItem.vue'

    export default {
  components: { BeTableHeader, BeTableItem, EditBuy, LoadingHuge },
        name: '',
        data() {
            return {
                buys: [ ]
            }
        },
        mounted() {
            this.buysLoading()
        },
        methods: {
            async buysLoading() {
                const res = await this.conn.get(this.api.buys, this.$store.state.token)

                if (res) {
                    this.buys = res
                }

            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


