<template>
    
    <tr class="table-td fs-14">
        <td class="ipv">
            {{ item.users_permissions_user.unid }}&nbsp;&nbsp;&nbsp;&nbsp;
        </td>
        <td class="text-left">
            {{ item.users_permissions_user.username }}
        </td>
        <td class="text-left">
            {{ item.product.named }}
        </td>
        <td class="ipv">
            {{ item.product.price }}
        </td>
        <td class="ipv">
            {{ item.product.pv }}
        </td>
        <td class="ipv">
            {{ item.num }}
        </td>
        <td class="buys">
            {{ num_ipv }}
        </td>
        <td class="ipv">
            <edit-buy :_item="item" @single_Father="updateItem"></edit-buy>
        </td>
    </tr>

</template>

<script>
import EditBuy from '@/components/Modal/EditBuy.vue'
import LoadingSmall from '@/components/Data/LoadingSmall.vue'
    export default {
  components: { EditBuy, LoadingSmall },
        props:{
            index: {
                type: Number
            },
            item: {
                type: Object
            },
            datas: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        computed: {
            num_ipv() {
                if (this.item) {
                    return Number.parseInt(this.item.product.pv) * 
                    Number.parseInt(this.item.num)
                }
            },
        },
        methods: {

            updateItem(product, num) {
                this.item.num = num 
                let res = this.$store.state.products.filter( e => {
                    if (e.id == product) {
                        return true
                    }

                    return false
                })

                this.item.product = res[0]

                setTimeout(() => {
                    this.$router.go(0)
                }, 600)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


